.home {
  min-height: 100vh;
  font-family: sans-serif;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  background-color: #4B6382;
}

.brand {
  display: flex;
  align-items: center;
}

.logo {
  width: 50px;
  margin-right: 10px;
}

.title {
  color: white;
  font-size: 24px;
}

/* Nav Buttons */
.nav-links {
  display: flex;
  gap: 20px;
}

.nav-links button {
  background: none;
  border: none;
  color: white;
  font-weight: bold;
  cursor: pointer;
  font-size: 16px;
}

.nav-links button:hover {
  text-decoration: underline;
}

/* Banner Section */
.banner {
  text-align: center;
  padding: 80px 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  color: white;
  min-height: 100vh;
}

.banner::before {
  content: "";
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.banner h2,
.banner h3,
.options {
  position: relative;
  z-index: 2;
}

.banner h2 {
  font-size: 36px;
  margin-bottom: 60px;
}

.banner h3 {
  font-size: 48px;
  margin-bottom: 200px; /* space between heading and icons */
}

/* Options Grid */
.options {
  display: flex;
  justify-content: center;
  gap: 25px;
  flex-wrap: wrap;
  margin-top: 25px; 
  /* smaller space between heading and icons */
  z-index: 2;
  position: relative;
}

/* Individual Box */
.option {
  background-color: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 10px;
  width: 130px;
  height: 140px;
  text-align: center;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  color: black;
  transition: transform 0.2s ease-in-out;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.option img {
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
}

.option:hover {
  transform: scale(1.05);
}
